$accent-colour: #ee0278
$disabled-colour: #999


@mixin accent-button
  display: flex
  justify-content: center
  align-items: center
  padding: 1em 1.5em
  margin: 1em
  background: $accent-colour
  color: #fff
  outline: none
  border: none
  border-radius: 4px
  cursor: pointer
  box-shadow: 0 3px 6px rgba(42, 42, 42, .2)

  &:disabled
    opacity: .2
    cursor: default
    background: $disabled-colour
    box-shadow: none


@mixin appbar
  display: flex
  justify-content: center
  align-items: center
  width: 100%
  padding: 0 1rem
  height: 3rem
  color: #666
  box-sizing: border-box

  > .back
    display: flex
    position: absolute
    justify-content: center
    align-items: center
    left: 1rem
    font-size: .9rem
