@import '../common.sass'


.appbar
  @include appbar


.select-pane 
  display: flex
  position: relative
  justify-content: center
  align-items: center
  flex-direction: column
  height: calc(100vh - 3rem)


.select-title
  display: flex
  justify-content: center
  align-items: center
  margin: 0 0 5rem
  font-size: 2rem


.level-list
  display: flex
  justify-content: space-around
  align-items: center
  flex-direction: column


.level-button
  @include accent-button
  width: 7em


.quiz-pane
  display: flex
  position: relative
  justify-content: space-between
  align-items: center
  flex-direction: column
  padding: 2rem 2rem 4rem
  height: calc(100vh - 3rem)
  box-sizing: border-box


.number-display
  display: block
  position: relative
  width: 100%
  color: #ccc
  font-size: 2rem


.quiz-content
  display: flex
  flex: 1 1 80%
  justify-content: space-around
  align-items: center
  flex-direction: column
  width: 100%


.quiz-instruction
  display: flex
  flex: 1 1 0%
  justify-content: flex-start
  align-items: center
  width: 80%


.quiz-requirements
  display: flex
  flex: 1 1 0%
  justify-content: flex-start
  align-items: flex-start
  flex-direction: column
  width: calc(80% - 2rem)
  margin: 0


.code-zone
  display: flex
  position: relative
  flex: 0 0 70%
  justify-content: center
  align-items: center
  border: 2px solid #aaa
  width: 90%
  padding: 2rem 
  box-sizing: border-box


.code-instruction
  display: block
  position: absolute
  top: 2rem
  right: 2rem
  bottom: 2rem
  left: 2rem
